// ==========================================
// RISIVO CRM - Workflow Execution Engine
// Core engine for processing workflow executions
// ==========================================

import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type {
  Workflow,
  WorkflowAction,
  WorkflowExecution,
  ActionExecutionResult,
  ActionType,
  ActionConfig,
  ExecutionStatus,
  ActionCondition
} from './types'

// ==========================================
// TYPES
// ==========================================

export interface ExecutionContext {
  executionId: string
  workflowId: string
  workflow: Workflow
  contactId?: string
  companyId?: string
  triggerData: Record<string, any>
  variables: Record<string, any>
  contact?: Record<string, any>
  company?: Record<string, any>
}

export interface ActionResult {
  success: boolean
  output?: Record<string, any>
  error?: string
  skipRemainingActions?: boolean
  nextActionId?: string // For go-to actions
}

// ==========================================
// WORKFLOW ENGINE CLASS
// ==========================================

export class WorkflowEngine {
  private supabase: typeof supabaseAdmin

  constructor(supabaseClient: any) {
    this.supabase = supabaseClient
  }

  // ==========================================
  // MAIN EXECUTION METHOD
  // ==========================================

  async executeWorkflow(executionId: string): Promise<void> {
    const startTime = Date.now()

    try {
      // Get execution details
      const { data: execution, error: execError } = await this.supabase
        .from('workflow_executions')
        .select(`
          *,
          workflows (
            *,
            workflow_actions (*)
          ),
          contacts (*)
        `)
        .eq('id', executionId)
        .single()

      if (execError || !execution) {
        await this.logError(executionId, 'unknown', 'Execution not found', { executionId })
        return
      }

      // Update status to running
      await this.updateExecutionStatus(executionId, 'running', {
        started_at: new Date().toISOString()
      })

      await this.log(executionId, execution.workflow_id, 'info', 'Workflow execution started', {
        workflowName: execution.workflows?.name
      })

      // Build execution context
      const context: ExecutionContext = {
        executionId,
        workflowId: execution.workflow_id,
        workflow: execution.workflows,
        contactId: execution.contact_id,
        companyId: execution.company_id,
        triggerData: execution.trigger_data || {},
        variables: execution.context_data || {},
        contact: execution.contacts || undefined
      }

      // Get sorted actions
      const actions = (execution.workflows?.workflow_actions || [])
        .sort((a: any, b: any) => a.execution_order - b.execution_order)

      if (actions.length === 0) {
        await this.log(executionId, execution.workflow_id, 'warning', 'No actions in workflow')
        await this.updateExecutionStatus(executionId, 'completed', {
          completed_at: new Date().toISOString()
        })
        return
      }

      // Execute actions sequentially
      let currentIndex = execution.current_action_index || 0
      let skipRemaining = false

      while (currentIndex < actions.length && !skipRemaining) {
        const action = actions[currentIndex]

        // Update current action index
        await this.supabase
          .from('workflow_executions')
          .update({ current_action_index: currentIndex })
          .eq('id', executionId)

        // Check conditions
        if (action.conditions && action.conditions.length > 0) {
          const conditionMet = await this.evaluateConditions(action.conditions, context)
          if (!conditionMet) {
            await this.recordActionResult(executionId, action.id, action.action_type, 'skipped', {
              reason: 'Conditions not met'
            })
            currentIndex++
            continue
          }
        }

        // Check for delay
        if (action.delay_minutes > 0) {
          // Schedule for later execution
          const scheduledFor = new Date(Date.now() + action.delay_minutes * 60 * 1000)
          await this.scheduleAction(executionId, action.id, scheduledFor)
          
          await this.updateExecutionStatus(executionId, 'paused', {
            current_action_index: currentIndex
          })
          
          await this.log(executionId, execution.workflow_id, 'info', 
            `Action scheduled for ${scheduledFor.toISOString()}`, { actionId: action.id })
          return // Exit, will be resumed later
        }

        // Execute action
        const result = await this.executeAction(action, context)

        // Record result
        await this.recordActionResult(
          executionId, 
          action.id, 
          action.action_type,
          result.success ? 'success' : 'failed',
          result.output,
          result.error
        )

        if (!result.success) {
          if (action.continue_on_error) {
            await this.log(executionId, execution.workflow_id, 'warning',
              `Action failed but continuing: ${result.error}`, { actionId: action.id })
          } else {
            // Stop execution on error
            await this.updateExecutionStatus(executionId, 'failed', {
              error_message: result.error,
              error_action_id: action.id,
              completed_at: new Date().toISOString()
            })
            return
          }
        }

        // Update context variables from action output
        if (result.output) {
          context.variables = { ...context.variables, ...result.output }
          await this.supabase
            .from('workflow_executions')
            .update({ context_data: context.variables })
            .eq('id', executionId)
        }

        // Handle special actions
        if (result.skipRemainingActions) {
          skipRemaining = true
        } else if (result.nextActionId) {
          // Go-to action: find the target action index
          const targetIndex = actions.findIndex((a: any) => a.id === result.nextActionId)
          if (targetIndex !== -1) {
            currentIndex = targetIndex
            continue
          }
        }

        currentIndex++
      }

      // Workflow completed
      const duration = Date.now() - startTime
      await this.updateExecutionStatus(executionId, 'completed', {
        completed_at: new Date().toISOString()
      })

      await this.log(executionId, execution.workflow_id, 'info', 
        `Workflow completed in ${duration}ms`, { totalActions: actions.length })

    } catch (error) {
      console.error('Workflow execution error:', error)
      await this.updateExecutionStatus(executionId, 'failed', {
        error_message: error instanceof Error ? error.message : 'Unknown error',
        completed_at: new Date().toISOString()
      })
    }
  }

  // ==========================================
  // ACTION EXECUTION
  // ==========================================

  async executeAction(action: any, context: ExecutionContext): Promise<ActionResult> {
    const startTime = Date.now()

    try {
      await this.log(context.executionId, context.workflowId, 'info',
        `Executing action: ${action.name} (${action.action_type})`, { actionId: action.id })

      // Import action executors dynamically to avoid circular deps
      const { ActionExecutors } = await import('./action-executors')
      const executor = ActionExecutors[action.action_type as ActionType]

      if (!executor) {
        return {
          success: false,
          error: `Unknown action type: ${action.action_type}`
        }
      }

      // Process variable substitution in config
      const processedConfig = this.substituteVariables(action.config, context)

      // Execute the action
      const result = await executor(processedConfig, context, this.supabase)

      const duration = Date.now() - startTime
      await this.log(context.executionId, context.workflowId, 
        result.success ? 'info' : 'error',
        `Action ${action.name} ${result.success ? 'completed' : 'failed'} in ${duration}ms`,
        { actionId: action.id, result })

      return result

    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Action execution failed'
      }
    }
  }

  // ==========================================
  // VARIABLE SUBSTITUTION
  // ==========================================

  substituteVariables(config: any, context: ExecutionContext): any {
    if (typeof config === 'string') {
      return this.substituteString(config, context)
    }
    
    if (Array.isArray(config)) {
      return config.map(item => this.substituteVariables(item, context))
    }
    
    if (typeof config === 'object' && config !== null) {
      const result: Record<string, any> = {}
      for (const [key, value] of Object.entries(config)) {
        result[key] = this.substituteVariables(value, context)
      }
      return result
    }
    
    return config
  }

  substituteString(str: string, context: ExecutionContext): string {
    // Replace {{variable}} patterns
    return str.replace(/\{\{([^}]+)\}\}/g, (match, varPath) => {
      const value = this.getValueByPath(varPath.trim(), context)
      return value !== undefined ? String(value) : match
    })
  }

  getValueByPath(path: string, context: ExecutionContext): any {
    const parts = path.split('.')
    const root = parts[0]
    const restPath = parts.slice(1)

    let value: any
    switch (root) {
      case 'contact':
        value = context.contact
        break
      case 'company':
        value = context.company
        break
      case 'trigger':
        value = context.triggerData
        break
      case 'variables':
        value = context.variables
        break
      default:
        value = context.variables[root]
    }

    for (const part of restPath) {
      if (value === undefined || value === null) return undefined
      value = value[part]
    }

    return value
  }

  // ==========================================
  // CONDITION EVALUATION
  // ==========================================

  async evaluateConditions(conditions: ActionCondition[], context: ExecutionContext): Promise<boolean> {
    if (conditions.length === 0) return true

    let result = this.evaluateSingleCondition(conditions[0], context)

    for (let i = 1; i < conditions.length; i++) {
      const condition = conditions[i]
      const conditionResult = this.evaluateSingleCondition(condition, context)
      
      if (condition.logicOperator === 'OR') {
        result = result || conditionResult
      } else {
        result = result && conditionResult
      }
    }

    return result
  }

  evaluateSingleCondition(condition: ActionCondition, context: ExecutionContext): boolean {
    const fieldValue = this.getValueByPath(condition.field, context)
    const compareValue = condition.value

    switch (condition.operator) {
      case 'equals':
        return fieldValue == compareValue
      case 'not_equals':
        return fieldValue != compareValue
      case 'contains':
        return String(fieldValue).includes(String(compareValue))
      case 'not_contains':
        return !String(fieldValue).includes(String(compareValue))
      case 'greater_than':
        return Number(fieldValue) > Number(compareValue)
      case 'less_than':
        return Number(fieldValue) < Number(compareValue)
      case 'is_empty':
        return fieldValue === null || fieldValue === undefined || fieldValue === ''
      case 'is_not_empty':
        return fieldValue !== null && fieldValue !== undefined && fieldValue !== ''
      case 'in':
        return Array.isArray(compareValue) && compareValue.includes(fieldValue)
      case 'not_in':
        return !Array.isArray(compareValue) || !compareValue.includes(fieldValue)
      default:
        return false
    }
  }

  // ==========================================
  // HELPER METHODS
  // ==========================================

  async updateExecutionStatus(
    executionId: string, 
    status: ExecutionStatus,
    additionalData: Record<string, any> = {}
  ): Promise<void> {
    await this.supabase
      .from('workflow_executions')
      .update({
        status,
        updated_at: new Date().toISOString(),
        ...additionalData
      })
      .eq('id', executionId)
  }

  async recordActionResult(
    executionId: string,
    actionId: string,
    actionType: string,
    status: 'success' | 'failed' | 'skipped',
    output?: Record<string, any>,
    error?: string
  ): Promise<void> {
    await this.supabase
      .from('action_execution_results')
      .insert({
        id: uuidv4(),
        workflow_execution_id: executionId,
        action_id: actionId,
        action_type: actionType,
        status,
        started_at: new Date().toISOString(),
        completed_at: new Date().toISOString(),
        output: output || {},
        error_message: error
      })
  }

  async scheduleAction(
    executionId: string,
    actionId: string,
    scheduledFor: Date
  ): Promise<void> {
    await this.supabase
      .from('scheduled_actions')
      .insert({
        id: uuidv4(),
        workflow_execution_id: executionId,
        action_id: actionId,
        scheduled_for: scheduledFor.toISOString(),
        status: 'pending'
      })
  }

  async log(
    executionId: string,
    workflowId: string,
    level: 'info' | 'warning' | 'error' | 'debug',
    message: string,
    metadata?: Record<string, any>
  ): Promise<void> {
    try {
      await this.supabase
        .from('workflow_logs')
        .insert({
          id: uuidv4(),
          workflow_execution_id: executionId,
          workflow_id: workflowId,
          level,
          message,
          metadata: metadata || {}
        })
    } catch (err) {
      console.error('Failed to write workflow log:', err)
    }
  }

  async logError(
    executionId: string,
    workflowId: string,
    message: string,
    metadata?: Record<string, any>
  ): Promise<void> {
    await this.log(executionId, workflowId, 'error', message, metadata)
  }
}

// ==========================================
// FACTORY FUNCTION
// ==========================================

export function createWorkflowEngine(): WorkflowEngine {
  return new WorkflowEngine(supabaseAdmin)
}

// ==========================================
// EXPORT FOR BACKGROUND PROCESSING
// ==========================================

export async function processWorkflowExecution(executionId: string): Promise<void> {
  const engine = createWorkflowEngine()
  await engine.executeWorkflow(executionId)
}

// ==========================================
// SCHEDULED ACTION PROCESSOR
// ==========================================

export async function processScheduledActions(): Promise<void> {
  const supabase = supabaseAdmin
  
  // Get pending scheduled actions that are due
  const { data: scheduledActions, error } = await supabase
    .from('scheduled_actions')
    .select('*')
    .eq('status', 'pending')
    .lte('scheduled_for', new Date().toISOString())
    .limit(100)

  if (error || !scheduledActions) {
    console.error('Error fetching scheduled actions:', error)
    return
  }

  for (const scheduled of scheduledActions) {
    try {
      // Mark as processing
      await supabase
        .from('scheduled_actions')
        .update({ status: 'processing' })
        .eq('id', scheduled.id)

      // Resume workflow execution
      const engine = new WorkflowEngine(supabase)
      await engine.executeWorkflow(scheduled.workflow_execution_id)

      // Mark as completed
      await supabase
        .from('scheduled_actions')
        .update({ 
          status: 'completed',
          processed_at: new Date().toISOString()
        })
        .eq('id', scheduled.id)

    } catch (err) {
      console.error('Error processing scheduled action:', err)
      await supabase
        .from('scheduled_actions')
        .update({ 
          status: 'failed',
          error_message: err instanceof Error ? err.message : 'Unknown error'
        })
        .eq('id', scheduled.id)
    }
  }
}
