import { NextRequest, NextResponse } from 'next/server'
import { handleInboundWebhookRequest } from '@/lib/accelerator/trigger-listener'

// ==========================================
// POST /api/v1/webhooks/[...path] - Handle inbound webhooks
// ==========================================
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ path: string[] }> }
) {
  try {
    const { path } = await params
    const endpointPath = path.join('/')
    
    // Get request body
    let payload: Record<string, any>
    try {
      payload = await request.json()
    } catch {
      payload = {}
    }

    // Get headers
    const headers: Record<string, string> = {}
    request.headers.forEach((value, key) => {
      headers[key.toLowerCase()] = value
    })

    // Process webhook
    const result = await handleInboundWebhookRequest(endpointPath, payload, headers)

    if (!result.success) {
      return NextResponse.json(
        { success: false, error: result.message },
        { status: result.message.includes('not found') ? 404 : 401 }
      )
    }

    return NextResponse.json({
      success: true,
      message: result.message,
      workflowsTriggered: result.workflowsTriggered
    })

  } catch (error) {
    console.error('Error processing webhook:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// Also handle GET for simple webhooks
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ path: string[] }> }
) {
  try {
    const { path } = await params
    const endpointPath = path.join('/')
    
    // Get query params as payload
    const { searchParams } = new URL(request.url)
    const payload: Record<string, any> = {}
    searchParams.forEach((value, key) => {
      payload[key] = value
    })

    // Get headers
    const headers: Record<string, string> = {}
    request.headers.forEach((value, key) => {
      headers[key.toLowerCase()] = value
    })

    // Process webhook
    const result = await handleInboundWebhookRequest(endpointPath, payload, headers)

    if (!result.success) {
      return NextResponse.json(
        { success: false, error: result.message },
        { status: result.message.includes('not found') ? 404 : 401 }
      )
    }

    return NextResponse.json({
      success: true,
      message: result.message,
      workflowsTriggered: result.workflowsTriggered
    })

  } catch (error) {
    console.error('Error processing webhook:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}
