// ==========================================
// RISIVO CRM - Trigger Listener System
// Event-driven workflow trigger management
// ==========================================

import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { TriggerType, TriggerFilter, Workflow } from './types'
import { processWorkflowExecution } from './workflow-engine'

// ==========================================
// TYPES
// ==========================================

export interface TriggerEvent {
  type: TriggerType
  subAccountId: string
  contactId?: string
  companyId?: string
  data: Record<string, any>
  timestamp?: string
}

export interface TriggerResult {
  success: boolean
  triggeredWorkflows: string[]
  errors?: string[]
}

// ==========================================
// MAIN TRIGGER HANDLER
// ==========================================

export async function handleTriggerEvent(event: TriggerEvent): Promise<TriggerResult> {
  const supabase = supabaseAdmin
  const triggeredWorkflows: string[] = []
  const errors: string[] = []

  try {
    // Find matching workflows
    const { data: workflows, error } = await supabase
      .from('workflows')
      .select(`
        *,
        workflow_actions (*)
      `)
      .eq('sub_account_id', event.subAccountId)
      .eq('trigger_type', event.type)
      .eq('status', 'active')

    if (error) {
      console.error('Error fetching workflows for trigger:', error)
      return { success: false, triggeredWorkflows: [], errors: [error.message] }
    }

    if (!workflows || workflows.length === 0) {
      return { success: true, triggeredWorkflows: [] }
    }

    // Process each matching workflow
    for (const workflow of workflows) {
      try {
        // Check if filters match
        const filtersMatch = await evaluateTriggerFilters(
          workflow.trigger_filters || [],
          event,
          workflow.trigger_config
        )

        if (!filtersMatch) {
          continue
        }

        // Check execution limits
        if (event.contactId) {
          const canExecute = await checkExecutionLimits(
            supabase,
            workflow,
            event.contactId
          )
          if (!canExecute) {
            continue
          }
        }

        // Create workflow execution
        const executionId = uuidv4()
        const { error: execError } = await supabase
          .from('workflow_executions')
          .insert({
            id: executionId,
            workflow_id: workflow.id,
            contact_id: event.contactId,
            company_id: event.companyId,
            status: 'pending',
            trigger_type: event.type,
            trigger_data: {
              ...event.data,
              triggered_at: event.timestamp || new Date().toISOString()
            },
            triggered_at: event.timestamp || new Date().toISOString(),
            context_data: {}
          })

        if (execError) {
          errors.push(`Failed to create execution for workflow ${workflow.id}: ${execError.message}`)
          continue
        }

        // Log the trigger event
        await supabase
          .from('workflow_logs')
          .insert({
            id: uuidv4(),
            workflow_execution_id: executionId,
            workflow_id: workflow.id,
            level: 'info',
            message: `Workflow triggered by ${event.type}`,
            metadata: {
              triggerType: event.type,
              contactId: event.contactId,
              triggerData: event.data
            }
          })

        triggeredWorkflows.push(executionId)

        // Queue for execution (in production, use a job queue)
        // For now, execute asynchronously
        processWorkflowExecution(executionId).catch(err => {
          console.error(`Error executing workflow ${executionId}:`, err)
        })

      } catch (workflowErr) {
        errors.push(`Error processing workflow ${workflow.id}: ${workflowErr}`)
      }
    }

    return {
      success: errors.length === 0,
      triggeredWorkflows,
      errors: errors.length > 0 ? errors : undefined
    }

  } catch (err) {
    console.error('Trigger handler error:', err)
    return {
      success: false,
      triggeredWorkflows: [],
      errors: [err instanceof Error ? err.message : 'Unknown error']
    }
  }
}

// ==========================================
// FILTER EVALUATION
// ==========================================

async function evaluateTriggerFilters(
  filters: TriggerFilter[],
  event: TriggerEvent,
  triggerConfig: Record<string, any>
): Promise<boolean> {
  if (filters.length === 0) {
    // Check trigger config specific filters
    return evaluateTriggerConfigFilters(event, triggerConfig)
  }

  let result = evaluateSingleFilter(filters[0], event)

  for (let i = 1; i < filters.length; i++) {
    const filter = filters[i]
    const filterResult = evaluateSingleFilter(filter, event)

    if (filter.logicOperator === 'OR') {
      result = result || filterResult
    } else {
      result = result && filterResult
    }
  }

  return result
}

function evaluateSingleFilter(filter: TriggerFilter, event: TriggerEvent): boolean {
  const fieldValue = getNestedValue(event.data, filter.field)
  const compareValue = filter.value

  switch (filter.operator) {
    case 'equals':
      return fieldValue == compareValue
    case 'not_equals':
      return fieldValue != compareValue
    case 'contains':
      return String(fieldValue).toLowerCase().includes(String(compareValue).toLowerCase())
    case 'not_contains':
      return !String(fieldValue).toLowerCase().includes(String(compareValue).toLowerCase())
    case 'greater_than':
      return Number(fieldValue) > Number(compareValue)
    case 'less_than':
      return Number(fieldValue) < Number(compareValue)
    case 'is_empty':
      return fieldValue === null || fieldValue === undefined || fieldValue === ''
    case 'is_not_empty':
      return fieldValue !== null && fieldValue !== undefined && fieldValue !== ''
    default:
      return true
  }
}

function evaluateTriggerConfigFilters(
  event: TriggerEvent,
  config: Record<string, any>
): boolean {
  // Check specific trigger type configurations
  switch (event.type) {
    case 'contact_tag':
      if (config.tagIds && config.tagIds.length > 0) {
        const eventTagId = event.data.tagId
        if (!config.tagIds.includes(eventTagId)) {
          return false
        }
      }
      if (config.tagOperation) {
        if (config.tagOperation !== 'any' && config.tagOperation !== event.data.operation) {
          return false
        }
      }
      break

    case 'appointment_status':
      if (config.appointmentStatuses && config.appointmentStatuses.length > 0) {
        if (!config.appointmentStatuses.includes(event.data.status)) {
          return false
        }
      }
      if (config.calendarIds && config.calendarIds.length > 0) {
        if (!config.calendarIds.includes(event.data.calendarId)) {
          return false
        }
      }
      break

    case 'call_status':
      if (config.callStatuses && config.callStatuses.length > 0) {
        if (!config.callStatuses.includes(event.data.status)) {
          return false
        }
      }
      break

    case 'email_events':
      if (config.emailEvents && config.emailEvents.length > 0) {
        if (!config.emailEvents.includes(event.data.eventType)) {
          return false
        }
      }
      break

    case 'opportunity_status_change':
      if (config.opportunityStatuses && config.opportunityStatuses.length > 0) {
        if (!config.opportunityStatuses.includes(event.data.newStatus)) {
          return false
        }
      }
      if (config.pipelineId && config.pipelineId !== event.data.pipelineId) {
        return false
      }
      break

    case 'pipeline_stage_changed':
      if (config.pipelineId && config.pipelineId !== event.data.pipelineId) {
        return false
      }
      if (config.stageId && config.stageId !== event.data.newStageId) {
        return false
      }
      break

    case 'form_submitted':
      if (config.formId && config.formId !== event.data.formId) {
        return false
      }
      break

    case 'trigger_links_clicked':
      if (config.linkId && config.linkId !== event.data.linkId) {
        return false
      }
      break
  }

  return true
}

function getNestedValue(obj: Record<string, any>, path: string): any {
  return path.split('.').reduce((current, key) => {
    return current?.[key]
  }, obj)
}

// ==========================================
// EXECUTION LIMITS CHECK
// ==========================================

async function checkExecutionLimits(
  supabase: any,
  workflow: any,
  contactId: string
): Promise<boolean> {
  // Check multiple executions setting
  if (!workflow.allow_multiple_executions) {
    const { count } = await supabase
      .from('workflow_executions')
      .select('*', { count: 'exact', head: true })
      .eq('workflow_id', workflow.id)
      .eq('contact_id', contactId)
      .neq('status', 'cancelled')

    if (count && count > 0) {
      return false
    }
  }

  // Check max executions per contact
  if (workflow.max_executions_per_contact) {
    const { count } = await supabase
      .from('workflow_executions')
      .select('*', { count: 'exact', head: true })
      .eq('workflow_id', workflow.id)
      .eq('contact_id', contactId)
      .neq('status', 'cancelled')

    if (count && count >= workflow.max_executions_per_contact) {
      return false
    }
  }

  return true
}

