// Birthday Reminder
export async function triggerBirthdayReminders(subAccountId: string): Promise<TriggerResult[]> {
  const supabase = supabaseAdmin
  const results: TriggerResult[] = []

  // Get contacts with birthday today
  const today = new Date()
  const monthDay = `${String(today.getMonth() + 1).padStart(2, '0')}-${String(today.getDate()).padStart(2, '0')}`

  const { data: contacts } = await supabase
    .from('contacts')
    .select('id')
    .eq('sub_account_id', subAccountId)
    .like('date_of_birth', `%-${monthDay}`)

  if (contacts) {
    for (const contact of contacts) {
      const result = await handleTriggerEvent({
        type: 'birthday_reminder',
        subAccountId,
        contactId: contact.id,
        data: { birthdayDate: today.toISOString() }
      })
      results.push(result)
    }
  }

  return results
}

