// ==========================================
// WEBHOOK ENDPOINT HANDLER
// ==========================================

export async function handleInboundWebhookRequest(
  endpointPath: string,
  payload: Record<string, any>,
  headers: Record<string, string>
): Promise<{ success: boolean; message: string; workflowsTriggered?: number }> {
  const supabase = supabaseAdmin

  // Find webhook endpoint
  const { data: endpoint, error } = await supabase
    .from('webhook_endpoints')
    .select('*')
    .eq('endpoint_path', endpointPath)
    .eq('is_active', true)
    .single()

  if (error || !endpoint) {
    return { success: false, message: 'Webhook endpoint not found' }
  }

  // Validate secret if configured
  if (endpoint.secret_key) {
    const providedSecret = headers['x-webhook-secret'] || headers['authorization']
    if (providedSecret !== endpoint.secret_key && providedSecret !== `Bearer ${endpoint.secret_key}`) {
      return { success: false, message: 'Invalid webhook secret' }
    }
  }

  // Validate headers if configured
  if (endpoint.expected_headers && Object.keys(endpoint.expected_headers).length > 0) {
    for (const [key, value] of Object.entries(endpoint.expected_headers)) {
      if (headers[key.toLowerCase()] !== value) {
        return { success: false, message: `Missing or invalid header: ${key}` }
      }
    }
  }

  // Update webhook stats
  await supabase
    .from('webhook_endpoints')
    .update({
      total_requests: endpoint.total_requests + 1,
      last_request_at: new Date().toISOString()
    })
    .eq('id', endpoint.id)

  // Trigger workflows
  const result = await onInboundWebhook(
    endpoint.sub_account_id,
    endpointPath,
    payload,
    payload.contactId || payload.contact_id
  )

  return {
    success: result.success,
    message: result.success ? 'Webhook processed successfully' : 'Failed to process webhook',
    workflowsTriggered: result.triggeredWorkflows.length
  }
}
